/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

 .alignwide .wp-block-wptravel-trip-search{
	max-width: initial !important;
 }

 .wptravel-block-trip-search{
	.wp-travel-search form {
		p{
			width: 100%;
			label{
				display: inherit;
			}
			input,select{
				line-height: 1.2;
				width: 100%;
			}
			input.button {
				height: unset;
				cursor: pointer;
			}
		}
	}
}

#wptravel-block-trip-search {
	.wp-travel-search p {
		width: 100%;
		margin: 0em;
		margin-bottom: 10px;
	}
}